<template>
  <v-app-bar max-height="64px" v-if="user.OperId != 0">
    <!-- Logo位置 -->
    <div
      class="pl-3 amber--text"
      style="width:120px;vertical-align:middle;text-align:left;cursor:pointer"
      @click="PG.setBottomSheet(true)"
    >
      TSTOCK
    </div>
    <v-tabs icons-and-text class="hidden-sm-and-down">
      <v-tab v-for="tab in tabs" :key="tab.name" @click="mtdClickTab(tab)">
        {{ tab.text }}
        <v-icon v-text="tab.icon"></v-icon>
      </v-tab>
    </v-tabs>
    <!-- <v-menu v-model="showMenu" offset-y max-width="120px" min-width="120px">
      <template v-slot:activator="{ on, attrs }">
        <v-tabs>
          <v-tab v-bind="attrs" v-on="on">
            {{tabs[6].text}}
          </v-tab>
        </v-tabs>
      </template>
      <v-list>
        <v-list-item v-for="(item,index) in items" :key="index" link>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu> -->
    <v-spacer />
    <!-- 自動填滿左右區塊 分配父子组件之间的剩餘寬度。 當一個 v-spacer 放置在子组件之前或之后时，组件将推到其容器的左右两侧。-->
    <v-tooltip top>
      <template v-slot:activator="{ on, attrs }">
        <v-btn text class="amber--text" v-bind="attrs" v-on="on">
          <v-icon>mdi-account </v-icon>{{ PG.getOper().OperName }}
        </v-btn>
      </template>
      <span>你好 {{ PG.getOper().OperName }}</span>
    </v-tooltip>
    <!-- 登出位置 -->
    <v-tooltip top>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          icon
          color="cyan"
          v-bind="attrs"
          v-on="on"
          @click="actInit(), mtdGoLoginPage()"
        >
          <v-icon>mdi-logout</v-icon>
        </v-btn>
      </template>
      <span>登出</span>
    </v-tooltip>

    <!-- 行動選單位置 -->
    <v-app-bar-nav-icon
      class="hidden-md-and-up"
      @click="PG.setNavigation(true)"
    >
    </v-app-bar-nav-icon>
    
  </v-app-bar>
  <v-app-bar max-height="64px" v-else> </v-app-bar>
</template>

<script>
import PG from "@/store/plugin.js";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      PG: PG,
      tabs: [
        {
          path: "/stockStatistics",
          text: "資訊看板",
          icon: "mdi-view-dashboard",
        },
        {
          path: "/stockDownload",
          text: "股價下載計算",
          icon: "mdi-chart-box",
        },
        {
          path: "/stockFavorite",
          text: "自選清單",
          icon: "mdi-hand-heart",
        },
        {
          path: "/stockProfit",
          text: "庫存損益",
          icon: "mdi-account-cash",
        },
        {
          path: "/stockData",
          text: "個股資訊查詢",
          icon: "mdi-chart-box",
        },
        {
          path: "/stockMemo",
          text: "個股資訊備註",
          icon: "mdi-chart-box",
        },
        {
          path: "/stockIndex",
          text: "上市櫃索引清單",
          icon: "mdi-database",
        },
      ],
      showMenu: true,
      items: [
        { title: "個股索引表" },
        { title: "系統Log" },
      ],
    };
  },
  created() {},
  mounted() {},
  computed: {
    ...mapState("global", ["navigation", "func"]),
    ...mapState("Login", ["user"]),
  },
  methods: {
    ...mapActions("Login", ["actInit"]),
    mtdClickTab(tab) {
      this.$router
        .push(tab.path)
        .then(() => {})
        .catch((err) => {
          err;
        });
    },
    mtdGoLoginPage() {
      this.$router
        .push("/login")
        .then(() => {})
        .catch((err) => {
          err;
        });
    },
  },
};
</script>
